import emitter from "./eventDispatcher.js";

export default class userEquipment extends Phaser.GameObjects.Container{

    constructor(scene,x,y,key){
        super(scene,x,y);

        scene.add.existing(this);
        this.mainSprite = scene.add.sprite(0,0,key);
        this.graphics = scene.add.graphics().setDepth(2);
        this.connectionIndictator = scene.add.sprite(0,0,'device_bars','default').setScale(0.1);
        this.userText = scene.add.text(0,40, '0%', { fontSize: '16px',fill: '#fff'}); 
        this.tLine = new Phaser.Curves.Line([ 0, 0, 0, 0 ]);  
        this.envelopeGroup = scene.add.container(0,0); //Idk why this needed to be a container
        this.add([this.mainSprite,this.graphics,this.userText,this.connectionIndictator,this.envelopeGroup]);


       
        //Unique Properties
        this.bars = 3;
        this.bsCoords = {x:0,y:0};
        this.bs_target = false;
        this.successCount = 0;
        this.begun = false;
        this.tethered = false; 
        //UI, Animations & Listeners
        this.on('pointerdown', this.handlePointerDown,this);
         //Base station selected
        emitter.on('bs_selected',(eventData) => {
            this.bs_target = true;
            if(!this.tethered) {
                this.frequency = eventData.f; 
                this.freqType = eventData.t;
            }
            if(eventData.x == this.bsCoords.x && eventData.y == this.bsCoords.y) return; //No need to run this function if its the same Base Station.
            //Except frequency, which could change.
            this.bsCoords = {x: eventData.x,y:eventData.y};
            const localCoords = this.getLocalPoint(this.bsCoords.x,this.bsCoords.y)
            this.tLine.p0.x = localCoords.x
            this.tLine.p0.y = localCoords.y
            this.speed =  Math.min(this.tLine.getLength()/60,8);
            this.duration = 3000 + 2.71 ** this.speed;

            if(this.duration > 7000){
                this.bars = 1;
            } else if(this.duration > 4000){
                this.bars = 2;
            } else {
                this.bars = 3;
            }
            [this.boxMove, this.paths] = this.newBoxAnim(this.scene,this.handleEnvelope,this.successHandler);
        })

        //Base station unselected
        emitter.on('bs_unselected', () => {
            this.bs_target = false;
        })

    }

    handlePointerDown(){
        this.tethered = this.bs_target;
        if(this.tethered) {
            emitter.emit('user_started',{x: this.bsCoords.x, y: this.bsCoords.y, f: this.frequency});
            this.connectionIndictator.setFrame(this.freqType + '_' + this.bars)
            this.disableInteractive();
        }
    }

    newBoxAnim(scene,handleComplete,handleSuccess) {
        var boxMove = scene.tweens.createTimeline({onComplete: handleSuccess, onCompleteScope:this});
        var paths = [];
        //var animTime = Math.round(this.duration * this.speed);
        for(var i = 0; i < this.envCount; i++){
            var temp_envelope = scene.add.sprite(-400,-400,'hd_envelope').setScale(0.2)
            .setVisible(true);
            var temp_path = {t: 0, vec: new Phaser.Math.Vector2()};
            paths.push(temp_path)
            this.envelopeGroup.add(temp_envelope);
            boxMove.add({targets: paths[i],
                t: 1,
                ease: 'linear',
                duration: this.duration,
                offset: 500*i,
                onComplete: handleComplete,
                repeat: 0,
                onCompleteScope: this
            });
        }
        return [boxMove, paths];

    }
    
    handleEnvelope() {
        emitter.emit('add_score',1);
        this.successCount += 1;
        //this.envCount -= 1; // Decrements envelopes by 1 for each successful data packet
        this.userText.setText(Phaser.Math.CeilTo(Phaser.Math.Percent(this.successCount,0,this.envCount)*100) + '%'); // Update the packetCount
        if(this.envCount == this.successCount){
            this.boxMove.emit('Success');
        }
    }

    boxAnim() {
        this.begun = true; 
        this.graphics.lineStyle(5, this.frequency, 1); //Red Line for Red Freq.
        this.graphics.fillStyle(0xfffffff, 1); //Envelopes
        this.tLine.draw(this.graphics);
        this.boxMove.play();
        for(var i = 0;i<this.paths.length;i++){
            this.tLine.getPoint(this.paths[i].t, this.paths[i].vec);
            var worldPoint = this.scene.cameras.main.getWorldPoint(this.paths[i].vec.x, this.paths[i].vec.y);
            this.envelopeGroup.list[i].setPosition(worldPoint.x, worldPoint.y);
            //this.graphics.fillRect(this.paths[i].vec.x - 8, this.paths[i].vec.y - 8, 16, 16);
        }
    }

    update(){
        this.graphics.clear();
        if(this.begun) this.disableInteractive();
        if(typeof this.boxMove != "undefined") {
            if(this.tethered || this.begun){
                this.boxAnim();
            } 
        }
    }

    /*
    reset_tweens() {
        for(var i=0;i<this.paths.length;i++) {
            this.boxMove.data[i].restart();
            this.paths[i].t = 0;
        }
    
    }*/
    
    successHandler(scene) {
        this.envelopeGroup.setVisible(false);
        this.boxMove.stop();
        emitter.emit('add_score',5);
        emitter.emit('user_finished',{x: this.bsCoords.x, y: this.bsCoords.y, f: this.frequency}); //This informs the Base Station that the user has finished.
        emitter.emit('userFinished'); // This updates the scene to spawn new entities.
        this.cleanup();
    }

    cleanup(){
        this.graphics.clear();
        this.setActive(false);
        this.setVisible(false);
        this.scene.active = false;
        //this.userText.destroy();
        //this.destroy();
    }
}
