class Pause extends Phaser.Scene {
    constructor(){
        super({key: 'pauseScene'});
    }

    create(){
        this.add.text(290,240,'Paused', {fontSize: '64px', fill: '#000', fontFamily: 'Gloria Hallelujah'})
        .setInteractive();
        this.add.text(235,330,'Wish this trick worked in real life', {fontSize: '20px', fontFamily: 'Gloria Hallelujah', fill: '#000'});
        var pause_button = this.add.sprite(this.game.config.width-25,25, 'pausebutton')
        .on('pointerdown', () => {
            this.scene.resume("freeplayScene");
            this.scene.stop();
        })
        .setScale(0.5);
        pause_button.scene.add.tween({
            targets: pause_button,
            scaleX: "*=0.8",
            scaleY: "*=0.8",
            duration: 120,
            onComplete: () => {
                pause_button.setInteractive();
            },
            yoyo: true
        });
    }

    update(time,delta){}
}

export default Pause;