import emitter from "./eventDispatcher.js";
import baseEquipment from "./baseEquipment.js";

export default class baseStation extends baseEquipment {
    constructor(scene,x,y){
        super(scene,x,y,'hd_baseStation');
        this.mainSprite.setScale(0.3);
        this.setSize(this.mainSprite.displayWidth,this.mainSprite.displayHeight);
        this.setInteractive();
        this.mainSprite.setInteractive();
        //Base Station Listeners for Frequency Channels
        emitter.on('new_frequency', (freqInfo) => {
            this.frequency = freqInfo.f;
            this.freqType = freqInfo.name;
        });

        emitter.on('user_started', (bsInfo) => {
            if(bsInfo.x === this.x+12 && bsInfo.y === this.y-50){
                this.channelsInUse.push(bsInfo.f);
            }
        })

        emitter.on('user_finished', (bsInfo) => {
            if(bsInfo.x === this.x+12 && bsInfo.y === this.y-50){
                this.channelsInUse = this.channelsInUse.filter(item => item !== bsInfo.f);
            }
        });
        this.channelsInUse = [];
        this.frequency = 0xec1c24; //Frequency for communication, default to red
        this.freqType = 'red'; //This makes navigating atlas related to frequency easier:)
        this.hover = false; //Is mouse hovering, might not be needed to initalise

        this.toggled = false; //This is how we tell if this BS is selected
        this.mainSprite.on('pointerover', () => {
            this.hover = true;
        });
        this.mainSprite.on('pointerout', () => {
            this.hover = false
        });
        scene.input.on('pointerdown',this.mouseClick,this);
        scene.input.on('pointermove',this.drawLineToPointer,this);
    }


    mouseClick(){
        if(!this.toggled & this.hover) {
            this.toggled = true;
            if(!this.channelsInUse.includes(this.frequency)) emitter.emit('bs_selected',{x: this.x+12, y: this.y-50, f: this.frequency, t: this.freqType});
            
        } else {
            this.graphics.clear();
            this.toggled = false;
            emitter.emit('bs_unselected');
        }
    }
    
    drawLineToPointer() {
        this.graphics.clear();
        if(this.toggled){
            this.graphics.lineStyle(5, this.frequency, 1.0);
            this.worldPoint = this.scene.cameras.main.getWorldPoint(this.scene.input.activePointer.x, this.scene.input.activePointer.y);
            this.graphics.beginPath();
            var headCoords = this.getLocalPoint(this.x+12,this.y-50);
            this.graphics.moveTo(headCoords.x, headCoords.y);
            this.graphics.lineTo(this.worldPoint.x-this.x, this.worldPoint.y-this.y);
            this.graphics.closePath();
            this.graphics.strokePath();
        }
      }



}