import baseStation from "../entities/baseStation.js"
import emitter from "../entities/eventDispatcher.js"
import Car from "../entities/car.js"
import Phone from "../entities/phone.js"
import relayBase from "../entities/relayBase.js"



class Tutorial extends Phaser.Scene {
    constructor() {
        super({
            key: 'tutorialScene'
        })
    }

    preload() { 
      
    }

    create() {
        this.textBoxCount = 0;
        this.allDevices = this.add.group({runChildUpdate: true});
        this.progress = true;
        this.txtScene = this.scene.get('textBoxScene');
        //Emitters
        emitter.on('user_started', function() {
            this.progress = true;
            this.events.emit('Progress');
            emitter.off('user_started');
        }, this);

        //textBox.on('pointerdown', () => {textBox.setVisible(false);});
        this.events.on('Progress', () => {
            switch(this.textBoxCount){
                case 1:    
                    this.mainTower = new baseStation(this,400,150).setScale(2)
                    this.txtScene.handleTextBoxUpdate(this.textBoxCount);
                    break;
                case 2: 
                    var tween = this.tweens.add({
                        targets: this.mainTower,
                        y: '+=30', // '+=100'
                        x: '-=300',
                        scale: 1,
                        ease: 'Linear', // 'Cubic', 'Elastic', 'Bounce', 'Back'
                        duration: 500,
                        repeat: 0, // -1: infinity
                        scope: this
                    });
                    break;
                case 3: 
                    this.phone_1 = new Phone(this,400,150,this.add_score,10000).setScale(1);
                    this.allDevices.add(this.phone_1);
                    this.txtScene.handleTextBoxUpdate(this.textBoxCount);
                    break;
                case 4: 
                    var tween = this.tweens.add({
                        targets: this.phone_1,
                        y: '+=70', // '+=100'
                        x: '-=150',
                        scale: 1,
                        ease: 'Linear', // 'Cubic', 'Elastic', 'Bounce', 'Back'
                        duration: 500,
                        repeat: 0, // -1: infinity
                        scope: this
                    }); 
                    textBox.start(text_4,30);
                    this.progress = false; 
                    break;
                case 5: 
                    if(this.progress) {
                        textBox.start(text_5,30);
                        this.progress = false;
                    }
                case 6: 
                    if(this.progress) {
                        textBox.start(text_6);
                        this.phone_2 = new Phone(this,700,300,this.add_score);
                        this.progress = false;
                    }
            }
        });


        this.scene.run('textBoxScene');
    }

    update() {}

    add_score(num){
        console.log('No scoring this time!')
    }

    handleTextBoxFinished(){
        this.input.enabled = true;
        this.textBoxCount += 1;
        this.events.emit('Progress')
    }
}


export default Tutorial;