class Gameover extends Phaser.Scene {
    constructor(){
        super({key: 'gameoverScene'})
    }
    
    init(data) {
        this.finalScore = data.finalScore;
    }
    create(){
        this.graphics = this.add.graphics().setDepth(2);
        this.pencil = this.add.image(230,445,'pencil').setVisible(false)
        .setScale(0.18)
        .setDepth(3);
        this.add.text(80,150,'Game over!', { fontSize: '120px', fill: '#000', fontFamily: 'Gloria Hallelujah'});
        this.add.text(210,300,'Final score: ' + this.finalScore, {fontSize: '60px', fill: '#000', fontFamily: 'Gloria Hallelujah'});
        const return_button = this.add.text(
            230,
            450,
            'Return to menu',
            {
                fontSize: '48px',
                fill: '#000',
                fontFamily: 'Gloria Hallelujah '
            }
        ).setInteractive()
        .setDepth(1);
        return_button.on('pointerup', () => this.exitMenu(return_button))    
        .on('pointerover', () => {
            this.pencil.setVisible(true);
        })
        .on('pointerout', () => {
            this.pencil.setVisible(false);
        });
    }

    update(time,delta){
        this.graphics.clear();
        this.graphics.lineStyle(6,0x00000,1);
        if(this.startDrawing && this.pencil.x>230){
          this.strikeThrough.p1.x = this.pencil.x-30;
          this.strikeThrough.draw(this.graphics);
        }
    }

    exitMenu(button){
        //Add pencil strikethrough
        this.strikeThrough = new Phaser.Curves.Line([ 230, button.y+42, this.pencil.x-30, button.y+42 ]);
        var pencilDuration = 300;
        this.input.enabled = false;
        this.startDrawing = true;
        this.tweens.add({
            targets:  this.pencil,
            x: button.width + this.pencil.x + 30,
            duration: pencilDuration,
        });     
        this.time.delayedCall(500, () => {
            this.cameras.main.fadeOut(1000,0,0,0);
            this.time.delayedCall(1000, () => {
                this.scene.stop('freeplayScene');
            this.scene.start('menuScene');
            })
        });
    }
}

export default Gameover;