import menuScene from './scenes/menu'
import preloaderScene from './scenes/preloader'
import freeplayScene from './scenes/freeplay'
import pauseScene from './scenes/pause'
import gameoverScene from './scenes/gameover'
import tutorialScene from './scenes/tutorial'
import theoryScene from './scenes/theory'
import textBoxScene from './scenes/textbox'
import emitter from './entities/eventDispatcher'

window.addEventListener('load', function () {

	var game = new Phaser.Game({ 
		width: 800,
		height: 600,
		type: Phaser.WebGL,
        backgroundColor: "#cccccc",
		scale: {
			mode: Phaser.Scale.FIT,
			autoCenter: Phaser.Scale.CENTER_BOTH
		},
    physics: {
      default: 'arcade'
    },
    plugins: {
      }

	});

	game.scene.add("Preload", preloaderScene);
	game.scene.add("Menu", menuScene);
	game.scene.add("Boot", Boot, true);
  game.scene.add("Freeplay", freeplayScene);
  game.scene.add("Tutorial", tutorialScene);
  game.scene.add("Theory", theoryScene);
  game.scene.add("Gameover", gameoverScene);
  game.scene.add("Pause", pauseScene);
  game.scene.add("Textbox",textBoxScene);
});

class Boot extends Phaser.Scene {
    constructor() {
      super({ key: 'bootScene' });
    }
  
    init(data) {}
  
    preload() {
      // Preload splash logo to be displayed in the preloader scene.
      this.load.image('logo', 'assets/phaser3-logo.png');

    }
  
    create(data) {
      // Start the preloader
      this.add.image(400,300,'logo');
      this.scene.launch('preloaderScene');
    }
  
  }