import emitter from "./eventDispatcher.js"
import userEquipment from "./userEquipment.js"

export default class Phone extends userEquipment {
    constructor(scene,x,y,scoreCallback,maxTime = 20){
        super(scene,x,y,'hd_phone',scoreCallback)
        this.mainSprite.setScale(0.3);
        this.connectionIndictator.setPosition(40,-50);
        this.setSize(this.mainSprite.displayWidth,this.mainSprite.displayHeight);
        this.setInteractive();
        this.envCount = Phaser.Math.RND.between(5,10);
        this.progressPerc = 100/this.envCount;
       // [this.boxMove, this.paths] = this.newBoxAnim(scene,this.handleEnvelope,this.successHandler);
        this.countdown = maxTime;
        this.alertText = scene.add.text(25,-40,'!', { font: '24px Arial', fill: '#FFFF00'})
        this.add(this.alertText);
        this.countEvent = scene.time.addEvent({
            delay: 1000,
            callback: () => {
                this.countdownUpdate(maxTime);
            },
            callbackScope: this,
            loop: true
        });
    }
    //Todo: Add graphical indication for countdown
    countdownUpdate(maxTime){
        if(!this.tethered){
            this.alertText.setVisible(true);
            this.countdown -= 1;
            if(this.countdown < maxTime/4) {
                this.alertText.setText('!!!');
                this.alertText.setColor('#FF0000');
            } else if(this.countdown< maxTime/2) {
                this.alertText.setText('!!');
                this.alertText.setColor('#FFA500')
            }

            if(this.countdown<0) emitter.emit('gameover',this);
        } else {
            this.alertText.setVisible(false);
        }
    }
}