import userComponent from './userComponent.js'

export default class FloatAnim extends userComponent{

    constructor(gameObject){
        super(gameObject)
        this.gameObject = gameObject;
        gameObject["__FloatAnim"] = this;
    }

    /** @returns {FloatAnim} */
	static getComponent(gameObject) {
		return gameObject["__FloatAnim"];
	}
    /** @type {Phaser.GameObjects.Image} */
	gameObject;

    awake() {

		this.scene.add.tween({
			targets: this.gameObject,
			y: "+=10", // Move the object 20 pixels down
            ease: "Sine.easeInOut",
            duration: 2500, // One second
            yoyo: true,
            delay: Phaser.Math.Between(100,1000),
            repeat: -1, // Repeat infinitely
            repeatDelay: 100
		});


    }
}