class Theory extends Phaser.Scene{
    constructor(){
        super({key: 'theoryScene'});
    }

    init(data) {}

  preload() {}


    create() {
        this.add.text(20,
            20,
            'This will have some theory about 5G, some of the challenges and some extended reading for those interested in some of the technologies hinted at.',
            {
                font: 'Arial 30px',
                fill: '#fff'
            }
            );
        this.add.text(600,500,'Back',{font: 'Arial 30x', fill: '#fff'}).setInteractive()
        .on('pointerup', () => this.scene.start('menuScene'));

    }
}

export default Theory;